<template>
  <div>
    <div class="row">
      <div class="col-3">
        <h3 class="fw-bold">Pages</h3>
      </div>
      <div class="col-6">
        <b-form-input size="lg" class="" placeholder="Search">
        </b-form-input>
      </div>
      <div class="col-3 test-decoration-none">
        <router-link to="/admin/pages/new">
          <v-btn tile color="error" large>
            <v-icon left>
              mdi-plus
            </v-icon>
            CREATE PAGE
          </v-btn>
        </router-link>
      </div>
    </div>
    <div class=" my-5">
      <v-data-table :headers="headers" :items="desserts" sort-by="calories" :single-select="singleSelect"
                    show-select class="elevation-1" v-model="checkedIds" >
        <template v-slot:top>
          <v-divider class="mx-4" inset vertical></v-divider>
          <v-spacer></v-spacer>
          <v-dialog v-model="dialogDelete" max-width="500px">
            <v-card>
              <v-card-title class="text-h6">Are you sure you want to delete this
                page?
              </v-card-title>
              <v-card-actions>
                <v-spacer></v-spacer>
                <v-btn color="blue darken-1" text @click="closeDelete">Cancel</v-btn>
                <v-btn color="blue darken-1" text @click="deleteItemConfirm">OK</v-btn>
                <v-spacer></v-spacer>
              </v-card-actions>
            </v-card>
          </v-dialog>
          <v-dialog v-model="deletemultiplepages" max-width="500px">
              <v-card>
                <v-card-title class="text-h6">Are you sure you want to delete these pages?</v-card-title>
                <v-card-actions>
                  <v-spacer></v-spacer>
                  <v-btn  class="cred" text @click="closeMultiDailoge">Cancel</v-btn>
                  <v-btn v-if="isLoading" class="cred">Loading...</v-btn>
                  <v-btn v-else class="cred" text @click="multiplepageIds" >OK</v-btn>
                  <v-spacer></v-spacer>
                </v-card-actions>
              </v-card>
            </v-dialog>
        </template>
        <template v-slot:[`item.action`]="{ item }">
          <router-link :to="{name: 'pagesedit',params: { page_id: item.id }}">
          <v-icon small class="mr-2" @click="editItem(item)">
            mdi-pencil
          </v-icon>
        </router-link>
          <v-icon small @click="deleteItem(item)"> mdi-delete</v-icon>
        </template>
        <template v-slot:[`item.active`]="{ item }">
          <v-icon small class="mr-2" @click="editItem(item)">
            mdi-check
          </v-icon>
        </template>
        <template v-slot:[`item.status`]="{ item }">
          <div v-if="item.status === '1'">
            <v-icon small class="mr-2">mdi-check</v-icon>
          </div>
          <div v-else>
            <v-icon small class="mr-2">mdi-close</v-icon>
          </div>
        </template>
        <template v-slot:footer>
          <div class="text-muted p-3">
            <v-btn  color="grey" @click="getSelectedIds">DELETE SELECTED</v-btn>
          </div>
        </template>
      </v-data-table>
      <v-snackbar v-model="snackbar" :timeout="timeout">{{ text }}
        <template v-slot:action="{attrs}">
          <v-btn color="blue" text v-bind="attrs" @click="snackbar = false">Close</v-btn>
        </template>
      </v-snackbar>
      
    </div>
  </div>
  
</template>

<script>
import axios from 'axios';
export default{
    name: "PagesView",
    components: {  },
    data: () => ({
        dialog: false,
        dialogDelete: false,
        checkedIds:[],
        headers: [
            { text: "Title", value: "page_name" },
            { text: "Avtive", align: 'end', value: "status" },
            { text: "Actions", align: 'end', value: "action", sortable: false },
        ],
        desserts: [],
        editedIndex: -1,
        editedItem: {
          id: "",
          column_id: "",
          page_name: "",
          status: "",
          page_status: "",
        },
        defaultItem: {
            title: "",
        },
        snackbar: false,
        text: '',
        timeout: '',
        isLoading:false,
        deletemultiplepages:false,
    }),

    computed: {
        formTitle() {
            return this.editedIndex === -1 ? "New Item" : "Edit Item";
        },
    },

    watch: {
        dialog(val) {
            val || this.close();
        },
        dialogDelete(val) {
            val || this.closeDelete();
        },
    },

    created() {
        this.initialize();
    },

    methods: {
        initialize() {
          axios.get("all/pages").then( (resp) => {
            this.desserts = resp.data.pages;
          });
             
        },

        editItem(item) {
            this.editedIndex = this.desserts.indexOf(item);
            this.editedItem = Object.assign({}, item);
            this.dialog = true;
        },

        deleteItem(item) {
            this.editedIndex = this.desserts.indexOf(item);
            this.editedItem = Object.assign({}, item);
            this.dialogDelete = true;
        },

        deleteItemConfirm() {
            this.desserts.splice(this.editedIndex, 1);
            this.closeDelete();
            var data = {
              id : this.editedItem.id
            };

            axios.post("delete/page",data).then( (resp) => {
              this.snackbar = true;
              this.text = resp.data.message;
              this.closeDelete();
              this.initialize();
              this.timeout = 3000;
            });
        },

        close() {
            this.dialog = false;
            this.$nextTick(() => {
                this.editedItem = Object.assign({}, this.defaultItem);
                this.editedIndex = -1;
            });
        },

        closeDelete() {
            this.dialogDelete = false;
            this.$nextTick(() => {
                this.editedItem = Object.assign({}, this.defaultItem);
                this.editedIndex = -1;
            });
        },

        save() {
            if (this.editedIndex > -1) {
                Object.assign(this.desserts[this.editedIndex], this.editedItem);
            } else {
                this.desserts.push(this.editedItem);
            }
            this.close();
        },

        getSelectedIds() {
          console.log("hello");
            var data = {
            ids:this.checkedIds.map(item => item.id)
          };
          if(data.ids.length == 0){
            this.snackbar = true;
            this.text = "Please select tabs!";
            this.timeout = 3000;
          }else{
          this.deletemultiplepages = true;
          }
        },

        closeMultiDailoge(){
          this.deletemultiplepages = false;
        },

        multiplepageIds(){
          this.isLoading = true;
          var data = {
            ids:this.checkedIds.map(item => item.id)
          };
          axios.post('delete/multiple/pages', data)
            .then((resp) => {
              if(resp.data.success == true){
                this.isLoading = false;
                this.initialize();
                this.deletemultiplepages = false;
                this.snackbar = true;
                this.text = resp.data.message;
                this.timeout = 3000;
              }else{
                this.isLoading = false;
                this.deletemultiplepages = false;
                this.initialize();
                this.snackbar = true;
                this.text = 'something went wrong';
                this.timeout = 3000;
              }
            });
        }
    },
}
</script>